//@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Poppins";
}

html {
  scroll-behavior: smooth;
}

p {
  color: rgb(85, 85, 85);
}

/*transition*/
a,
.btn {
  transition: all 300ms ease;
}

/*desktop nav*/
.nav-links {
  display: flex;
  gap: 2rem;
  list-style: none;

  a {
    color: black;
    text-decoration: none;
    text-decoration-color: white;

    &:hover {
      text-decoration: underline;
      text-underline-offset: 1rem;
      text-decoration-color: rgb(181, 181, 181);
    }
  }
}

.logo {
  font-size: 2rem;

  &:hover {
    cursor: default;
  }
}

nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 17vh;
}

//hamburger menu
.hamburger-nav {
  display: none;
}

.hamburger-menu {
  position: relative;
  display: inline-block;
}

.hamburger-icon {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 24px;
  width: 30px;
  cursor: pointer;

  span{
    width: 100%;
    height: 2px;
    background: black;
    transition: all 300ms ease-in-out;
  }
}

.menu-links{
    position: absolute;
    top: 100%;
    right: 0%;
    background: white;
    width: fit-content;
    max-height: 0;
    overflow: hidden;
    transition: all 300ms ease-in-out;

    a{
        display: block;
        padding: 10px;
        text-align: center;
        font-size: 1.5rem;
        color: black;
        text-decoration: none;
        transition: all 300ms ease-in-out;
    }
    li{
        list-style: none;
    }
}

.menu-links.open{
    max-height: 300px;
}

.hamburger-icon.open span:first-child{
    transform: rotate(45deg) translate(10px, 5px);
}

.hamburger-icon.open span:nth-child(2){
    opacity: 0;
}

.hamburger-icon.open span:last-child{
    transform: rotate(-45deg) translate(10px, -5px);
}
